import styles from "./Register.module.css";
import {
  Button,
  PasswordInput,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Redirect } from "react-router-dom";
import { useState } from "react";
import { registration } from "../services/action/authorization";
import { useDispatch, useSelector } from "react-redux";

export default function Register() {
  const dispatch = useDispatch();
  const { isAuthenticated } = useSelector(
    (store) => store.authorizationReducer
  );

  const [form, setForm] = useState({
    mail: "",
    password: "",
    name: "",
  });
  const onChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const onRegistration = (e) => {
    e.preventDefault();
    dispatch(registration(form.mail, form.password, form.name));
  };

  if (isAuthenticated) {
    return <Redirect to="/" />;
  }

  return (
    <main className={styles.main__page}>
      <div className={styles.container}>
        <h2 className={`text text_type_main-medium mb-6 ${styles.title}`}>
          
        </h2>
        <form onSubmit={onRegistration} className={styles.form} action="">
          <div className={"mb-6"}>
            <Input
              type={"text"}
              placeholder={""}
              onChange={onChange}
              value={form.name}
              name={"name"}
              error={false}
              errorText={""}
              size={"default"}
            />
          </div>
          <div className={"mb-6"}>
            <Input
              value={form.mail}
              onChange={onChange}
              name={"mail"}
              placeholder={"E-mail"}
              type={"email"}
              errorText={""}
              size={"default"}
            />
          </div>
          <div className={"mb-6"}>
            {" "}
            <PasswordInput
              onChange={onChange}
              value={form.password}
              name={"password"}
            />
          </div>
          <Button type="primary" size="medium">
            
          </Button>
        </form>
        <p className={"text text_type_main-default mt-20 mb-4"}>
           ?
          <Link to="/login" className={styles.span}>
            
          </Link>
        </p>
      </div>
    </main>
  );
}
